@charset "UTF-8";
@font-face {
  font-family: "wild-icons";
  src: url("../fonts/wild-icons.eot");
  src: url("../fonts/wild-icons.eot?#iefix") format("eot"), url("../fonts/wild-icons.woff2") format("woff2"), url("../fonts/wild-icons.woff") format("woff"), url("../fonts/wild-icons.ttf") format("truetype"), url("../fonts/wild-icons.svg#wild-icons") format("svg");
}

.icon-alarm:before, .icon-graph:before, .icon-speech:before, .icon-tumblr:before {
  font-family: "wild-icons";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-decoration: none;
  text-transform: none;
}

.icon-alarm:before {
  content: "";
}

.icon-graph:before {
  content: "";
}

.icon-speech:before {
  content: "";
}

.icon-tumblr:before {
  content: "";
}
